        <h2 class="float-left text-white font-bold leading-loose pt-2 pl-4 text-sm pb-1"><i class="fa fa-paypal text-white"></i> &nbsp; Paypal</h2>
        <button v-on:click="modal2 = !modal2" class="float-right border-r-0 border-b-0 border-t-0 w-10 h-10 text-white hover:text-gray-400 text-sm focus:outline-none"><i class="fa fa-times text-xs"></i></button>
        <div class="float-left bg-gray-900 block w-full"></div>
        <div class="float-left p-3 text-white text-sm">

              <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_top">
              <input type="hidden" name="cmd" value="_s-xclick" />
              <input type="hidden" name="hosted_button_id" value="8GLRCJJTZCMSA" />
              <input type="image" src="https://www.paypalobjects.com/en_US/GB/i/btn/btn_donateCC_LG.gif" border="0" name="submit" title="PayPal - The safer, easier way to pay online!" alt="Donate with PayPal button" />
              <img alt="" border="0" src="https://www.paypal.com/en_GB/i/scr/pixel.gif" width="1" height="1" />
              </form>

        </div>
      </div>
    </div>








    <div class="flex content-center justify-center container mx-auto mt-6 mb-6">
      <img src="@/assets/logo.png" width="200" height="50" class="block" />
    </div>
    

    <div class="bg-gray-200 shadow-sm w-full sm:w-2/5 md:w-2/5 lg:w-2/5 xl:w-2/5 border-0 sm:border p-0 sm:p-6 rounded block">

    <div class="flex flex-wrap">
      <div class="w-full sm:w-1/3">
      
        <label class="text-xl text-gray-600 inline-block mr-2">What is</label>

        <input type="number" v-model="percentage" class="w-16 m-0 inline-block border py-1 px-3 mt-2 mb-2 text-md font-bold text-gray-600 focus:outline-none hover:border-gray-400 focus:border-gray-500 focus:bg-gray-100" /> 
      
