    </div>

      <!--<label class="text-xl text-gray-600 inline-block">What is</label>

      <input type="number" v-model="percentage" class="w-16 m-0 inline-block border py-1 px-3 mt-2 mb-2 text-md font-bold text-gray-600 focus:outline-none hover:border-gray-400 focus:border-gray-500 focus:bg-gray-100" /> 

      <span class="text-xl text-gray-600">&#37; of</span>

      <input type="number" v-model="price" class="w-20 m-0 ml-1 inline-block border py-1 px-3 mt-2 mb-2 text-md font-bold text-gray-600 focus:outline-none hover:border-gray-400 focus:border-gray-500 focus:bg-gray-100 mr-2" />

      <label class="text-xl text-gray-600 block mt-4">Answer:</label>
      <h3 class="inline-block text-2xl font-bold text-gray-800 whitespace-pre-line">{{answer}}&#37;</h3>-->


 <label class="text-xl text-gray-600 block mb-2 mr-2">Donate:</label>

