          type="button"
          class="rounded bg-white text-gray-500 hover:text-gray-600 focus:outline-none text-xs h-12 text-center w-12 mr-1"
          accesskey=""
        >
          <i class="fab fa-btc fa-2x"></i>
        </button>

     <button
          v-on:click="modal2 = !modal2"
          type="button"
          class="rounded bg-white text-gray-500 hover:text-gray-600 focus:outline-none text-xs h-12 text-center w-12"
          accesskey=""
        >
          <i class="fab fa-paypal fa-2x"></i>
        </button>

    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      price: '1000',
      percentage: '10',
      modal: false,
      modal2: false
    }
  },
  computed: {
    answer() {  
      // return parseInt(this.price).toFixed(0) * parseInt(this.percentage / 100);
      return ((this.percentage / 100) * this.price).toFixed(2);
    }
  }
}
</script>

<style scoped>
</style>



